@use "../utils/mixins" as *;

.header {
  background-color: #000;
  background-image: url("../images/header-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  padding: 0 20px 94px;
  box-sizing: border-box;
  grid-template-rows: 1fr 1fr;

  @include grid();

  @include for-tablet() {
    background-image: url("../images/header-background-tablet.jpg");
  }

  @include for-desktop() {
    background-image: url("../images/header-background-desktop.jpg");
  }

  &__navigation {
    align-self: start;
    grid-column: 1 / 3;
    grid-row: 1 / 2;

    @include for-desktop() {
      grid-column: 1 / 13;
    }

    @include for-tablet() {
      grid-column: 1 / 7;
    }
  }

  &__title {
    align-self: end;
    grid-column: 1 / 3;
    grid-row: 2 / 3;

    @include for-desktop() {
      grid-column: 1 / 13;
    }

    @include for-tablet() {
      grid-column: 1 / 5;
    }
  }
}
